.b-about {
    background: $bg-color-lighter-grey;
    
    &-left, &-right {
        display: inline-block;
        vertical-align: middle;
    }

    &-left {
        width: 46%;       
        text-align: right;
        padding-top: 1em;
        background: $bg-color-light-grey;
        

        img {
            width: 29.3125em;
            margin-right: -1em;
        }
    }

    &-right {
        width: 50%;
        padding: 20px 0 20px 96px;

        p {
            width: 100%;
            max-width: 450px;
            font-size: 1.2em;
            line-height: 1.6;
        }
    }

    &.smallp {
        p {
            font-size: 15px;
        }
    }
}

.b-about-two {
    height: 42em;

    &-left, &-right {
        position: relative;
        height: 100%;
        display: inline-block;
        vertical-align: top;
    }

    &-left {
        width: 65%;
        text-align: right;
        padding-top: 7.6em;
        padding-right: 4em;

        ul {
            width: 45em;
            display: inline-block;
        }
    }

    &-right {
        width: 35%;
        margin-left: -7px;
        //background: linear-gradient(to top, $bg-color-pink-grey 66%, $bg-color 0%); 

        img {
                max-height: 100%;
                position: absolute;
                z-index: 10;
                top: -2em;
                left: -1em;
        }
    }

    &-bg {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 38em;
        height: 30em;
        background-color: $bg-color-pink-grey;
    }
}

@media(max-width: 960px){
    .b-about {
        &-right {
            padding-left: 50px;
        }
    }
}

@media(max-width: 800px){
    .b-about {
        &-left {
            width: 32%;
        }

        &-right {
            width: 66%;
        }
    }
}

@media(max-width: 560px){
	.b-about {
        &-left {
            width: 100%;
            padding: 20px 10px 16px;
            text-align: center;
            
            img {
                width: auto;
                max-height: 300px;
                margin: 0;
            }
        }
        
        &-right {
            width: 100%;
            padding: 30px 20px;

            p {
                max-width: 100%;
            }
        }
    }
}